/* App.css */

/* // Notas sobre los estilos:
// 1. * aplica un reinicio global eliminando los márgenes y el padding de todos los elementos para asegurar un estilo consistente.
// 2. body establece la fuente global a "Karla", sans-serif, color de texto #323232 y una altura de línea de 1 para un estilo limpio y legible en todo el documento.
// 3. .hidden define una clase para ocultar elementos cambiando la visibilidad a hidden y la opacidad a 0, haciendo los elementos invisibles pero manteniendo su espacio en el flujo del documento.
*/

* {
  margin: 0;
  padding: 0;
}

body {
  font-family: "Karla", sans-serif;
  color: #323232;
  line-height: 1;
}

.hidden {
  visibility: hidden;
  opacity: 0;
}
