/* Carrusel.css */

/* // Notas sobre los estilos:
// 1. .carrusel-container es el contenedor del carrusel que ocupa todo el ancho disponible, con un margen superior e inferior para espaciarlo adecuadamente en la pantalla.
// 2. .titulo-topVentas es el título para la sección de top ventas que está centrado con márgenes para separar del contenido adyacente.
// 3. .titulo-carrusel es el título del carrusel con un fondo color #fdf7c6, padding para el espaciado interno, y se muestra en línea bloque para ajustar el ancho al contenido.
// 4. .slide es el contenedor de cada diapositiva del carrusel con un fondo color #fdf7c6, padding, y estilos de flexbox para centrar el contenido vertical y horizontalmente, con un mínimo alto para asegurar que las diapositivas sean visibles.
// 5. .titulo es el título dentro de cada diapositiva con márgenes, color, tamaño de fuente, alineación de texto y un fondo color #c5b8fd para destacar, con padding para el espaciado interno.
// 6. .imgCarrusel define el tamaño de las imágenes dentro del carrusel con un ajuste de objeto para cubrir el área asignada sin distorsionarse.
// 7. .slick-arrow son los estilos para las flechas de navegación del carrusel con posición absoluta, centrado vertical, bordes redondeados, tamaño definido, y un cursor de puntero para indicar interactividad.
// 8. .slick-prev y .slick-next definen la posición de las flechas de navegación a la izquierda y derecha respectivamente.
// 9. .slick-arrow::before define el estilo del icono dentro de las flechas con un tamaño de fuente y color específico.
*/

@media (max-width: 360px) {

  .carrusel-container {
    width: 100%;
    max-width: 150rem;
    margin: 1em auto;
    margin-bottom: 0.5rem;
  }


  .titulo-topVentas {
    text-align: center;
    margin-top: 0.5rem;
    margin-bottom: 1rem;
  }


  .titulo-carrusel {
    background-color: #fdf7c6;
    display: inline-block;
    padding: 0.5em;
  }


  .slide {
    width: 100%;
    background-color: #fdf7c6;
    min-height: 20em;
    padding: 2em;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    box-sizing: border-box;
  }


  .titulo {
    margin-top: 1em;
    color: #323232;
    font-size: 1.5em;
    text-align: center;
    width: 90%;
    background-color: #c5b8fd;
    display: inline-block;
    padding: 0.5rem 1rem;


  }


  .imgCarrusel {
    width: 18.5rem;
    height: 10.6rem;
    object-fit: cover;
  }


  /* Estilos para las flechas del carrusel */
  .slick-arrow {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    border-radius: 50%;
    width: 2rem;
    height: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    z-index: 1;
  }


  .slick-prev {
    left: 1px;
  }


  .slick-next {
    right: 1px;
  }


  .slick-arrow::before {
    font-size: 1.5rem;
    color: #554c7c;
  }
}