/* Carrito.css */

/* // Notas sobre los estilos:  
// 1. .carrito-overlay es el contenedor del overlay del carrito que cubre toda la pantalla con una posición fija, centrada, con un fondo semitransparente negro y un z-index de 1000 para asegurar que se muestre encima de otros elementos.
// 2. .carrito-content es el contenedor del contenido del carrito con un fondo color #c8b738, padding de 1rem, bordes redondeados de 5px, sombra alrededor y un máximo ancho de 20rem para asegurar que se vea bien en pantallas pequeñas.
// 3. .carrito-header es el encabezado del carrito con un margen inferior de 1rem para separar del contenido.
// 4. .carrito-contenedor es el contenedor de los productos del carrito con un margen superior de 1rem para separar del encabezado.
// 5. .producto es el contenedor individual de cada producto con un borde inferior, padding, y estilos de flexbox para alinear y justificar el contenido.
// 6. .producto:last-child quita el borde inferior del último producto de la lista para mejor apariencia.
// 7. .producto span define el tamaño de la fuente de los textos dentro del producto.
// 8. .cantidad es el contenedor de los botones de cantidad con un margen a la izquierda automático para alinearlo a la derecha.
// 9. .cantidad button define los estilos de los botones de cantidad con márgenes, padding, borde, fondo, color y cursor personalizados.
// 10. .btn-menos, .btn-mas y .btn-borrar definen el ancho específico de los botones para reducir, aumentar y borrar productos del carrito.
*/

@media (max-width: 360px) {

  .carrito-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
    background-color: rgba(0, 0, 0, 0.5);
  }

  .carrito-content {
    background-color: #c8b738;
    padding: 1rem;
    border-radius: 5px;
    box-shadow: 0 0 1.25rem rgba(0, 0, 0, 0.2);
    max-width: 20rem;
    width: 90%;
    max-height: 80vh;
    overflow-y: auto;
    position: relative;
  }

  .carrito-header {
    margin-bottom: 1rem;
  }

  .carrito-contenedor {
    margin-top: 1rem;
  }

  .producto {
    border-bottom: 1px solid #554c7c;
    padding: 0.5rem 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .producto:last-child {
    border-bottom: none;
  }

  .producto span {
    font-size: 1em;
  }

  .cantidad {
    margin-left: auto;
  }

  .cantidad button {
    margin: 0 0.25rem;
    padding: 0.25rem 0.5rem;
    border: 1px solid #554c7c;
    border-radius: 3px;
    background-color: #fefbe3;
    color: #554c7c;
    cursor: pointer;
  }

  .btn-menos {
    width: 0.9375rem;
  }

  .btn-mas {
    width: 0.9375rem;
  }

  .btn-borrar {
    width: 0.9375rem;
  }

}