/* Plataformas.css */

/* // Notas sobre los estilos:
// 1. .Categorias define el contenedor para las categorías con alineación central del texto, padding para espaciado interno y un fondo color #c5b8fd.
// 2. .title--accion y .title--deporte son los títulos de las secciones de acción y deporte, centrados, con un margen inferior, fuente Lato en negrita y tamaño de 1.5em.
// 3. .recomendados--categoriasAccion y .recomendados--categoriasDeporte son contenedores flexibles que envuelven su contenido y distribuyen los elementos de manera uniforme con un margen superior para separar del contenido anterior.
// 4. .categoriaAccion y .categoriaDeporte son contenedores de elementos individuales de categoría con flexbox, centrado de contenido, márgenes, padding, bordes redondeados y sombra, ocupando el 50% del ancho menos el margen con un máximo de 7rem.
// 5. .deporte tiene un pequeño margen superior adicional para separar del contenido previo.
// 6. .categoriaAccion img y .categoriaDeporte img definen el tamaño y ajuste de las imágenes dentro de las categorías para cubrir el área asignada sin distorsión.
// 7. .categoriaAccion span y .categoriaDeporte span establecen márgenes superiores e inferiores y un tamaño de fuente de 0.9em para los textos dentro de las categorías.
// 8. .btn-comprar define los estilos de los botones de compra con márgenes, padding, fondo color #554c7c, texto blanco, bordes redondeados, cursor de puntero y una transición de color de fondo para el efecto hover.
// 9. .btn-comprar
cambia el fondo del botón al color #332d4a cuando se coloca el cursor sobre él.
// 10. .accion y .categoriaAccion establecen el fondo color #c5b8fd y #fefbe3 para las secciones y categorías de acción respectivamente.
// 11. .deporte y .categoriaDeporte establecen el fondo color #fefbe3 y #c5b8fd para las secciones y categorías de deporte respectivamente.
*/

@media (max-width: 360px) {
  
  .Categorias {
    text-align: center;
    padding: 1.25rem 0; 
    background-color: #c5b8fd; 
  
  }
  
  .title--accion,
  .title--deporte {
    text-align: center;
    margin-bottom: 0.625rem;
    font-family: "Lato", sans-serif;
    font-size: 1.5em;
    font-weight: bold;
  }
  
  .recomendados--categoriasAccion,
  .recomendados--categoriasDeporte {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around; 
    width: 100%;
    margin-top: 1.25rem;
  }
  
  .categoriaAccion,
  .categoriaDeporte {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin: 0.625rem;
    padding: 0.625rem;
    border-radius: 5px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 1);
    width: calc(50% -  0.625rem); 
    max-width: 7rem; 
  }
  
  .deporte {
    margin-top: 0.25rem;
  }
  
  .categoriaAccion img,
  .categoriaDeporte img {
    width: 100%; 
    max-width: 100%; 
    height: auto; 
    object-fit: cover;
  }
  
  .categoriaAccion span,
  .categoriaDeporte span {
    margin-top: 2.5px;
    margin-bottom: 2px; 
    font-size: 0.9em; 
  }
  
  .btn-comprar {
    margin-top: 0.625rem;
    padding: 5px 0.625rem;
    background-color: #554c7c;
    color: #fff;
    border: none;
    border-radius: 3px;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .btn-comprar:hover {
    background-color: #332d4a;
  }
  
  /* Estilos específicos para la sección de Acción */
  .accion {
    background-color: #c5b8fd;
  }
  
  .categoriaAccion {
    background-color: #fefbe3;
  }
  
  /* Estilos específicos para la sección de Deportes */
  .deporte {
    background-color: #fefbe3;
  }
  
  .categoriaDeporte {
    background-color: #c5b8fd;
  }
  
}
