/* Footer.css */

/* // Notas sobre los estilos:
// 1. .footer define el contenedor del pie de página con un fondo color #c5b8fd, padding para espaciado interno y un peso de fuente de 500.
// 2. .container es un contenedor flexible con dirección de columna y contenido centrado para asegurar que todos los elementos se alineen verticalmente y estén centrados.
// 3. .yellow-background es una sección dentro del footer con un fondo color #fefbe3 y padding para espaciado interno.
// 4. .messages es un contenedor de mensajes con flexbox en dirección de columna, alineación central y un margen inferior para separar del contenido siguiente.
// 5. .messages p establece el tamaño de fuente de los párrafos dentro del contenedor de mensajes a 0.8rem.
// 6. .logo define márgenes superiores para la sección de logo dentro del footer.
// 7. .logo img establece el ancho de las imágenes de logo a 5rem.
// 8. .contact-icons es un contenedor de iconos de contacto con flexbox y justificación centrada para alinear los iconos en el centro.
// 9. .contacto_icon es un contenedor de iconos de contacto con flexbox, 100% de ancho y un margen superior para separar del contenido anterior.
// 10. .contacto_icon img define el tamaño de los iconos de contacto a 1rem de ancho con márgenes laterales para espaciarlos uniformemente.
*/

@media (max-width: 360px) {
  .footer {
    background-color: #c5b8fd;
    padding: 1.25rem;
    font-weight: 500;
  }

  .container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .yellow-background {
    background-color: #fefbe3;
    padding: 1rem;
  }

  .messages {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 1.5rem;
  }

  .messages p {
    font-size: 0.8rem;
  }

  .logo {
    margin-top: 1rem;
  }

  .logo img {
    width: 5rem;
  }

  .contact-icons {
    display: flex;
    justify-content: center;
  }

  .contacto_icon {
    display: flex;
    width: 100%;
    margin-top: 1.5rem;
  }

  .contacto_icon img {
    width: 1rem;
    margin: 0 1.5rem;
  }

}