/* Reseñas.css */

/* // Notas sobre los estilos:
// 1. .reseñas-section es el contenedor principal de la sección de reseñas con flexbox en columna, alineación centrada, padding y fondo color #c5b8fd.
// 2. .header-container organiza el encabezado con flexbox, justificación entre elementos y alineación centrada, ocupando todo el ancho.
// 3. .reseñas-title define el título de la sección con tamaño de fuente de 1.2rem, color #323232, fuente 'Lato' y peso de 700.
// 4. .icono-mas es el icono de añadir reseña con ancho de 1.5rem, altura de 2rem, cursor de puntero, transición de transform 0.3s y padding a la derecha de 1rem.
// 5. .btn-reseñas aplica un margen a la izquierda de 1rem a los botones de reseñas.
// 6. #reseñas-contenedor es el contenedor de las reseñas con flexbox, envoltura, justificación centrada, margen auto, padding de 1.25rem y fondo color #c5b8fd.
// 7. .resena define el estilo de cada comentario de reseña con flexbox en columna, alineación a la izquierda, márgenes, padding, borde redondeado, sombra y fondo color #f9f9f9.
// 8. .resena
aplica una sombra más pronunciada al pasar el cursor sobre la reseña.
// 9. .resena h3 define el estilo del título de la reseña con margen inferior, peso de fuente bold, color #323232 y tamaño de fuente de 1.1em.
// 10. .resena .usuario y .titulo-reseña definen estilos para el nombre del usuario y el título con color, peso de fuente y alineación.
// 11. .resena .comentario-reseña establece el estilo del comentario con tamaño de fuente de 1em, color #666, márgenes, y altura de línea de 1.4.
// 12. .iconos-acciones aplica un margen superior a los contenedores de iconos de acciones.
// 13. .icono-borrar y .icono-editar definen el estilo de los iconos de borrar y editar con ancho, altura, cursor de puntero, transición de transform 0.3s y padding para el icono de borrar.
*/

@media (max-width: 360px) {

  .reseñas-section {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0.5rem;
    background-color: #c5b8fd;
  }

  .header-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }

  .reseñas-title {
    font-size: 1.2rem;
    color: #323232;
    font-family: 'Lato', sans-serif;
    font-weight: 700;
  }

  .icono-mas {
    width: 1.5rem;
    height: 2rem;
    cursor: pointer;
    transition: transform 0.3s;
    padding-right: 1rem;
  }

  .btn-reseñas {
    margin-left: 1rem;
  }

  #reseñas-contenedor {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 0 auto;
    padding: 1.25rem;
    background-color: #c5b8fd;
  }

  /* Estilos para los comentarios de reseñas */
  .resena {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    text-align: left;
    margin: 0.625rem;
    padding: 1rem;
    padding-bottom: 1.5rem;
    border-radius: 0.625rem;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    background-color: #f9f9f9;
    box-sizing: border-box;
    transition: box-shadow 0.3s;
    width: calc(100% - 1.25rem);
  }

  .resena:hover {
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
  }

  .resena h3 {
    margin-bottom: 0.625rem;
    font-weight: bold;
    color: #323232;
    font-size: 1.1em;
  }

  .resena .usuario {
    color: #888;
    font-weight: 600;
  }

  .resena .titulo-reseña {
    color: #323232;
    font-weight: 600;
  }

  .resena .comentario-reseña {
    font-size: 1em;
    color: #666;
    margin-top: 1rem;
    margin-bottom: 1rem;
    line-height: 1.4;
  }

  .iconos-acciones {
    margin-top: 1rem;
  }

  .icono-borrar {
    width: 1.5rem;
    height: 2rem;
    cursor: pointer;
    transition: transform 0.3s;
    padding-right: 1rem;
  }

  .icono-editar {
    width: 1.5rem;
    height: 2rem;
    cursor: pointer;
    transition: transform 0.3s;
  }


}