/* Footer.css */

/* // Notas sobre los estilos:
// 1. .Plataformas es el contenedor principal de la sección de plataformas con texto centrado, fondo color #c5b8fd y padding vertical de 1.25rem.
// 2. .titulo-plataformas es el título de la sección de plataformas con fondo color #fefbe3, display en línea y padding de 0.5rem a 1rem.
// 3. .logos es el contenedor de los logotipos con flexbox, justificación centrada y flex-wrap para permitir que los elementos se ajusten y envuelvan según sea necesario.
// 4. .logos img define los estilos de las imágenes de los logotipos con un margen de 1.5rem y un ancho de 2rem.
*/

@media (max-width: 360px) {
 
  .Plataformas {
    text-align: center;
    background-color: #c5b8fd;
    padding: 1.25rem 0; 
  }
 
  .titulo-plataformas {
    background-color: #fefbe3;
    display: inline-block;
    padding: 0.5rem 1rem;
  }
 
  .logos {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
  }
 
  .logos img {
    margin: 1.5rem; 
    width: 2rem;
  }
 
}
