/* Modal.css */

/* // Notas sobre los estilos:
// 1. .modal es el contenedor del modal con posición fija, centrado en la pantalla, ancho del 90%, padding, sombra, z-index alto, transición suave y fondo color #c8b738.
// 2. .modal__header define el encabezado del modal con margen inferior y una altura de línea de 1.5.
// 3. .modal__form es el formulario dentro del modal con una estructura de grid de dos columnas, alineación central y un gap de 1rem entre los elementos.
// 4. .modal__form label define los estilos de las etiquetas del formulario con un tamaño de fuente de 1rem.
// 5. .modal__form input es el campo de entrada del formulario con padding, borde de 1px sólido y bordes redondeados.
// 6. .modal__form button ocupa ambas columnas del grid, está centrado y tiene un margen superior.
// 7. .btn--close-modal es el botón de cierre del modal con posición absoluta en la esquina superior derecha, sin borde ni fondo y un cursor de puntero.
// 8. .btn--close-modal tiene herencia de la familia y color de fuente, con un tamaño de fuente de 2rem.
// 9. .btn-login es el botón de inicio de sesión con padding, sin borde, bordes redondeados, cursor de puntero y una transición de color de fondo suave.
// 10. .overlay es el fondo oscuro que cubre toda la pantalla detrás del modal con posición fija, z-index de 999 y un color de fondo semitransparente.
// 11. .overlay.hidden oculta el overlay estableciendo display a none.
// 12. .btn-login
cambia el fondo del botón de inicio de sesión a color #8c844a cuando se coloca el cursor sobre él.
// 13. .register-text define el texto de registro con un margen superior y un tamaño de fuente de 0.8rem.
// 14. .register-text a define el estilo de los enlaces dentro del texto de registro con color #554c7c y sin subrayado.
// 15. .register-text a
subraya el enlace cuando se coloca el cursor sobre él.
// 16. .btn--logout define el botón de cierre de sesión con posición absoluta en la esquina inferior derecha.
*/

@media (max-width: 360px) {

  .modal {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 90%;
    padding: 2rem;
    box-shadow: 0 2rem 4rem rgba(0, 0, 0, 0.3);
    z-index: 1000;
    transition: all 0.5s;
    background-color: #c8b738;
  }

  .modal__header {
    margin-bottom: 2rem;
    line-height: 1.5;
  }

  .modal__form {
    margin: 0;
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;
    gap: 1rem;
  }

  .modal__form label {
    font-size: 1rem;
  }

  .modal__form input {
    padding: 0.5rem;
    border: 1px solid #ddd;
    border-radius: 0.5rem;
  }

  .modal__form button {
    grid-column: 1 / -1;
    justify-self: center;
    margin-top: 1rem;
  }

  .btn--close-modal {
    position: absolute;
    top: 0.5rem;
    right: 1rem;
    cursor: pointer;
    border: none;
    background: none;
  }

  .btn--close-modal {
    font-family: inherit;
    color: inherit;
    font-size: 2rem;
  }

  .btn-login {
    padding: 1rem 2rem;
    border: none;
    border-radius: 0.5rem;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }

  .overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 999;
  }

  .overlay.hidden {
    display: none;
  }

  .btn-login:hover {
    background-color: #8c844a;
  }

  .register-text {
    display: block;
    margin-top: 1rem;
    font-size: 0.8rem;
  }

  .register-text a {
    color: #554c7c;
    text-decoration: none;
  }

  .register-text a:hover {
    text-decoration: underline;
  }

  .btn--logout {
    position: absolute;
    bottom: 2rem;
    right: 2rem;
  }
}