/* Header.css */

/* // Notas sobre los estilos:
// 1. .header es el contenedor principal de la cabecera con padding, flexbox en dirección de fila, contenido espaciado entre sí, posición sticky para permanecer visible en la parte superior y fondo blanco con un z-index de 1000 para asegurar que esté por encima de otros elementos.
// 2. .nav_menu define el menú de navegación con un ancho de 2rem, sin margen izquierdo y cursor de puntero para indicar interactividad.
// 3. .logo define el logo con un ancho de 4.2rem y sin márgenes.
// 4. .busqueda es el contenedor del buscador con flexbox y alineación central de los elementos hijos.
// 5. .nav_busqueda es el icono de búsqueda con cursor de puntero.
// 6. .input-busqueda define el campo de entrada de búsqueda con padding, sin borde, bordes redondeados y margen derecho para separación del botón de búsqueda.
// 7. .btn-busqueda es el botón de búsqueda con padding, fondo color #554c7c, texto blanco, sin borde, bordes redondeados y una transición de color de fondo para el efecto hover.
// 8. .btn-busqueda
cambia el fondo del botón al color #332d4a cuando se coloca el cursor sobre él.
// 9. .carrito y .user son iconos con un ancho de 1.3rem y márgenes verticales.
// 10. .nav es el contenedor de navegación con flexbox, alineación central, ancho completo, contenido espaciado entre sí y un borde inferior de 2px sólido color #7165a5.
// 11. .icon-container es el contenedor de iconos con flexbox en dirección de columna y alineación central.
// 12. .btn-reseñas es un botón con texto color #554c7c, negrita, sin decoración, padding, borde de 2px sólido, bordes redondeados y transiciones de color de fondo y texto para el efecto hover.
// 13. .btn-reseñas
cambia el fondo a color #554c7c y el texto a blanco cuando se coloca el cursor sobre él.
// 14. .nuevo-nav es un nuevo contenedor de navegación con flexbox en dirección de columna, contenido espaciado y centrado, ancho completo, fondo blanco y padding para espaciado interno.
// 15. .categoria-group es un contenedor de grupo de categorías con flexbox, márgenes y justificación central.
// 16. .genero-btn es un botón de género con texto color #323232, sin decoración, padding, borde de 2px sólido color #554c7c, bordes redondeados, transiciones de color de fondo y texto para el efecto hover, margen y tamaño de fuente.
// 17. .genero-btn
cambia el fondo a color #554c7c y el texto a blanco cuando se coloca el cursor sobre él.
// 18. .hidden define un elemento oculto con display none.
*/

@media (max-width: 360px) {
  .header {
    padding: 0 1rem;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    position: sticky;
    top: 0;
    background-color: #fff;
    z-index: 1000;
  }

  .nav_menu {
    width: 2rem;
    margin-left: 0;
    cursor: pointer;
  }

  .logo {
    width: 4.2rem;
    margin: 0;
  }

  .busqueda {
    display: flex;
    align-items: center;
  }

  .nav_busqueda {
    cursor: pointer;
  }

  .input-busqueda {
    padding: 0.5em 1em;
    border: none;
    border-radius: 3px;
    margin-right: 0.5em;
  }

  .btn-busqueda {
    padding: 0.5em 1em;
    background-color: #554c7c;
    color: #fff;
    border: none;
    border-radius: 3px;
    cursor: pointer;
    transition: background-color 0.3s;
  }

  .btn-busqueda:hover {
    background-color: #332d4a;
  }

  .carrito, .user {
    width: 1.3rem;
    margin: 0.5rem 0;
  }

  .nav {
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: space-between;
    border-bottom: 2px solid #7165a5;
  }

  .icon-container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .carrito {
    margin: 0;
  }

  .user {
    margin: 0;
  }

  .btn-reseñas {
    color: #554c7c;
    font-weight: bold;
    text-decoration: none;
    padding: 0.5em 1em;
    border: 2px solid #554c7c;
    border-radius: 5px;
    transition: background-color 0.3s, color 0.3s;
  }
  
  .btn-reseñas:hover {
    background-color: #554c7c;
    color: #fff;
  }

  .nuevo-nav {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    background-color: #FFFFFF;
    padding: 1rem 0;
  }

  .categoria-group {
    display: flex;
    margin: 0.5rem;
    justify-content: center;
  }

  .genero-btn {
    color: #323232;
    text-decoration: none;
    padding: 0.5rem 1rem;
    border: 2px solid #554c7c;
    border-radius: 5px;
    transition: background-color 0.3s, color 0.3s;
    margin: 0.25rem;
    font-size: 0.875rem;
  }

  .genero-btn:hover {
    background-color: #554c7c;
    color: #fff;
  }

  .hidden {
    display: none;
  }
}
